import React from 'react';
import MovementsView from '../Base/MovementsView';

// Vista mvil para Ventas Diarias: formulario de ventas/compras del da y totales
const SalesMovilView = (props) => {
  // Aqu podras filtrar y mostrar solo ventas/compras del da y los totales requeridos
  return (
    <div className="relative min-h-screen bg-gray-50 pb-24">
      {/* Ttulo principal se muestra desde MovementsView, no aqu para evitar duplicados */}
      <div className="bg-white rounded-lg shadow p-3">
        {/* Puedes personalizar MovementsView para mostrar solo ventas/compras del da y totales */}
        <MovementsView {...props} showOnlySalesOfDay={true} />
      </div>
    </div>
  );
};

export default SalesMovilView;
